@keyframes fadeIn {
  0% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
}
@-webkit-keyframes fadeIn {
 0% {
     opacity:0;
 }
 100% {
     opacity:1;
 }
}
@keyframes fadeInTop {
  0% {
      opacity:0;
      transform:translateY(100%);
  }
  100% {
      opacity:1;
      transform:translateY(0);
  }
}
@-webkit-keyframes fadeInTop {
  0% {
      opacity:0;
      transform:translateY(100%);
  }
  100% {
      opacity:1;
      transform:translateY(0);
  }
}
