.button {
  padding: 1.125rem 1.5rem;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;

  border: none;
  outline: none;

  cursor: pointer;

  &--green {
    background-color: $basic-green;
    color: $white;

    &:hover {
      background-color: $hover-green;
    }

    &:active {
      background-color: $hover-green;
      opacity: 0.6;
    }
  }
  &--large {
    width: 100%;
  }
}
