@mixin pseudo-element($content: "", $pos: absolute, $display: block) {
  content: $content;
  position: $pos;
  display: $display;
}

@mixin reset-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin v-center {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin mobile {
  @media (min-width: $mobile-width) {
    @content;
  }
}

@mixin bgmobile {
  @media (min-width: $bgmobile-width) {
    @content;
  }
}

@mixin mdmobile {
  @media (min-width: $mdmobile-width) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: $tablet-width) {
    @content;
  }
}

@mixin laptop {
  @media (min-width: $laptop-width) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}

@mixin bgdesktop {
  @media (min-width: $bgdesktop-width) {
    @content;
  }
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}
