.work__header {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2.5rem;
}

.work__item-image__block {
  position: relative;
  padding: 0 0 65% 0;
  overflow: hidden;

  @include tablet {
    padding: 0 0 80% 0;
  }
}

.work__item-image {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  object-fit: cover;
}

.work__item-info {
  padding-top: 1.5rem;
  display: flex;
  align-items: flex-start;

  @include tablet {
    padding-top: 2rem;
  }
}

.work__item-date {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 0 0 4.0625rem;

  flex: 0 0 5rem;

  width: 5rem;
  height: 5rem;

  background: $basic-green;

  @include tablet {

  }

  @include desktop {
    flex: 0 0 6.25rem;

    width: 6.25rem;
    height: 6.25rem;
  }
}

.work__item-day {
  font-size: 2rem;
  font-weight: 700;
  line-height: 1;
  color: $white;
}

.work__item-month {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
  color: $white;
}

.work__item-link-block {
  padding-left: 1.625rem;
}

.work__item-link {
  &:hover {
    .work__item-title {
      color: $basic-green;
    }
  }

  &--arrow {
    padding-top: 1rem;
  }
}
