.numbers {
  padding-bottom: 3.75rem;

  @include tablet {
    padding-bottom: 5rem;
  }

  @include desktop {
    padding-bottom: 6.25rem;
  }

  .numbers__info {
    padding: 2.5rem 0;
    grid-column: span 4;

    @include tablet {
      grid-column: span 12;
    }

    @include desktop {
      grid-column: span 4;
    }
  }

  .numbers__text {
    padding-top: 1rem;
    max-width: 24.5rem;
  }

  .numbers__comment {
    padding-top: 1.875rem;
    color: $basic-green;
  }

  .numbers__item {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 2.5rem 2.1875rem;

    min-height: 18.125rem;

    &:after {
      @include pseudo-element;
      display: none;
      top: 1.625rem;
      right: 2.5rem;
      width: 18.9375rem;
      height: 14.9375rem;
      background-size: cover;
      z-index: 1;

      @include bgmobile {
        display: block;
      }
    }

    &--education {
      background-color: $light-blue;

      &:after {
        background-image: url(/img/icon-education.svg);
      }
    }
    &--medicine {
      background-color: $light-green;

      &:after {
        background-image: url(/img/icon-medical.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top right;
      }
    }
  }

  .numbers__title {
    padding-bottom: 1.875rem;
    font-size: 2rem;
    line-height: 1.2;
    font-weight: 700;
    color: $black;
    z-index: 2;

    @include tablet {
      font-size: 2.5rem;
      line-height: 2.875rem;
    }

    @include desktop {
      font-size: 3.0625rem;
      line-height: 3.5rem;
    }
  }

  .numbers__more {
    z-index: 2;
  }
}
