$screen-xs:                   0 !default;
$screen-sm:                   480px !default;
$screen-md:                   768px !default;
$screen-lg:                   992px !default;
$screen-xl:                   1200px !default;
$screen-xxl:                  1800px !default;


$width-container:1592px;

$decktop-padding: 16px;
$tablet-padding: 30px;
$small-padding: 20px;

$grid-columns:12;
$column-padding: 40px;
$grid-gap: 40px;


.wrapper {
    margin: auto;
    width:100%;
    padding-left: $small-padding;
    padding-right: $small-padding;

    @media (min-width: $screen-md) {
        max-width: $width-container;
        padding-left: $tablet-padding;
        padding-right: $tablet-padding;
    }
    @media (min-width: $screen-xl) {
      padding-left: $decktop-padding;
      padding-right: $decktop-padding;
    }
}

.container {
  display: flex;
  flex-wrap: wrap;

  &--grid {
    justify-content: center;

    @media (min-width: $screen-md) {
      justify-content: space-between;
    }

    @supports (display: grid) {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-column-gap: 2.5rem;
      grid-row-gap: 2.5rem;

      @media (min-width: $screen-md) {
        grid-template-columns: repeat(12, 1fr);
      }
    }

    .container__item {
      flex: 1 1 100%;

      @media (min-width: $screen-md) {
        flex: 0 1 calc((100% - 2.5rem)/2);
      }

      @media (min-width: $screen-xl) {
        flex: 0 1 calc((100% - 2.5rem*2)/3);
      }

      @supports (display: grid) {
        grid-column: span 4;

        @media (min-width: $screen-md) {
          grid-column: span 6;
        }

        @media (min-width: $screen-xl) {
          grid-column: span 4;
        }
      }
    }
  }
}


@mixin get-width ($column: 1, $columns: $grid-columns) {
    $width: (100% / $columns) * $column;
    flex: 0 0 $width;
    max-width: $width;
}

@mixin make-col (
  $column:  1,
  $columns: $grid-columns,
  $gutter:  $column-padding
) {
  @include get-width($column, $columns);

  @if ($gutter != false) {
    padding-left: ($gutter / 2);
    padding-right: ($gutter / 2);
  }
}

@mixin col (
  $screen:  xs,
  $column:  $grid-columns,
  $columns: $grid-columns,
  $gutter:  $column-padding
) {

  @if ($screen == '') {
    @include make-col($column, $columns, $gutter);
  }

  @if ($screen == xs) {
    @include make-col($column, $columns, $gutter);
  }

  @if ($screen == sm) {

    @media (min-width: $screen-sm) {
      @include make-col($column, $columns, $gutter);
    }
  }

  @if ($screen == md) {

    @media (min-width: $screen-md) {
      @include make-col($column, $columns, $gutter);
    }
  }

  @if ($screen == lg) {

    @media (min-width: $screen-lg) {
      @include make-col($column, $columns, $gutter);
    }
  }

  @if ($screen == xl) {

    @media (min-width: $screen-xl) {
      @include make-col($column, $columns, $gutter);
    }
  }

  @if ($screen == xxl) {

    @media (min-width: $screen-xxl) {
      @include make-col($column, $columns, $gutter);
    }
  }
}


.col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-10, .col-xl-11, .col-xl-12,
.col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12,
.col-xs-1, .col-xs-2, .col-xs-3, .col-xs-4, .col-xs-5, .col-xs-6, .col-xs-7, .col-xs-8, .col-xs-9, .col-xs-10, .col-xs-11, .col-xs-12,
.col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12,
.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
    position: relative;
    width: 100%;
    min-height: 1px;
}

@for $i from 1 through 12 {
  .col-#{$i} {
    @include col('', $i);
  }
}

@media (min-width: $screen-xs) {
  @for $i from 1 through 12 {
    .col-xs-#{$i} {
      @include col(xs, $i);
    }
  }
}

@media (min-width: $screen-sm) {
  @for $i from 1 through 12 {
    .col-sm-#{$i} {
      @include col(sm, $i);
    }
  }
}

@media (min-width: $screen-md) {
  @for $i from 1 through 12 {
    .col-md-#{$i} {
      @include col(md, $i);
    }
  }
}

@media (min-width: $screen-xl) {
  @for $i from 1 through 12 {
    .col-xl-#{$i} {
      @include col(xl, $i);
    }
  }
}
