*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  padding: 0;
  font-family: $gilroy;
  font-weight: 400;
  font-display: swap;
  color: $darkgrey;
  background-color: $white;
}

a {
  text-decoration: none;
  color: $darkgrey;
  cursor: pointer;

  &:hover {
    color: $basic-green;
  }

  &:active {
    color: $basic-green;
    opacity: 0.75;
  }
}

h1, h2, h3 {
  font-family: $gilroy;
}

p {
  font-size: 1rem;
  line-height: 1.625rem;
}

.text-uppercase {
  text-transform: uppercase;
}

img {
  max-width: 100%;
  height: auto;
}

.list-style-none {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  overflow: hidden;
}
