.blog {
  background-color: $light-blue;

  .blog-item {
    display: flex;
    flex-direction: column;
  }

  .blog-item__image-container {
    position: relative;
    overflow: hidden;
    padding: 0 0 65% 0;

    @include tablet {
      padding: 0 0 80% 0;
    }
  }

  .blog-item__image {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  .blog-item__content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 1.875rem;
    background-color: $white;
  }

  .blog-item__subtitle {
    margin-bottom: 0.625rem;
    letter-spacing: 1px;
    color: $black;
  }

  .blog-item__description {
    margin-top: 1rem;
    color: $darkgrey;
  }

  .blog-item__icon-container {
    padding-top: 1.5625rem;
    margin-top: auto;
  }
}
