.footer {
  .footer-subscribe__title {
    margin-bottom: 0.5rem;
  }

  .footer-subscribe, .footer-contact, .footer-menu {
    grid-column: span 4;

    @include tablet {
      grid-column: span 12;
    }
    @include desktop {
      grid-column: span 4;
    }
  }

  .footer-contact__group {
    &:last-of-type {
      padding-top: 2.25rem;
    }
  }

  .footer-subscribe__form {
    padding-top: 1rem;
  }

  .footer-contact {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include desktop {
      align-items: center;
    }
  }

  .footer-subscribe__social {
    padding-top: 1rem;

    .social-icon__link {
      &--facebook {
        .social-icon {
          width: 19px;
          height: 35px;
        }
      }
      &--twitter {
        .social-icon {
          width: 33px;
          height: 27px;
        }
      }
      &--instagram {
        .social-icon {
          width: 34px;
          height: 34px;
        }
      }
    }
  }

  .footer-menu{
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include desktop {
      align-items: center;
    }
  }

  .footer-subscribe__nav {
    &--bottom {
      padding-top: 2.25rem;
    }

    .footer-subscribe__nav-item {
      padding: 0.2rem 0;
    }
  }
}
