.banner {
  .banner__wrapper {
    position: relative;
    margin: auto;
    padding: 0 1.25rem 2.5rem 1.25rem;

    @include tablet {
      padding: 0 1.875rem 3.75rem 1.875rem;
    }

    @include desktop {
      padding: 0 1rem 5rem 1rem;
      max-width: 1592px;
    }

    @include bgdesktop {
      padding: 0 1rem 7.1875rem 1rem;
      max-width: 1680px;
    }
  }

  .banner__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-column: span 4;

    position: relative;

    .banner__text {
      padding-top: 3rem;

      @include tablet {
        padding-top: 40%;
      }

      @include bgdesktop {
        padding-top: 16rem;
      }
    }

    .banner__title {
      padding-bottom: 4.5rem;
      margin-bottom: 0;
      font-size: 1.875rem;
      line-height: 2.5rem;
      font-weight: 500;
      color: $black;

      @include tablet {
        font-size: 1.25rem;
        line-height: 1.5625rem;
      }

      @include desktop {
        max-width: 29rem;
        font-size: 2.5625rem;
        line-height: 3.125rem;
      }

      @include bgdesktop {
        padding-bottom: 7.5rem;
      }
    }

    .banner__button {
      .banner__button-link {
        position: relative;
        padding: 12px 20px 0 18px;
        font-size: 1.125rem;
        font-weight: 700;
        color: $black;

        @include desktop {
          font-size: 1.5rem;
        }

        &:before {
          @include pseudo-element;
          top:0;
          left: 0;
          width: 50%;
          height: 45px;
          background-color: $basic-green;
          z-index: -1;

          transition: all 0.2s ease-in-out;

          @include desktop {
            height: 55px;
          }
        }

        &:hover {
          color: $black;

          &:before {
            width: 100%;
          }
        }
      }
    }

    .banner__social {
      margin-top: 4rem;
      width: 5.8125rem;

      @include tablet {
        margin-top: auto;
      }

      .social-icon__link {
        &--facebook {
          svg {
            width: 9px;
            height: 17px;
          }
        }
        &--twitter {
          svg {
            width: 16px;
            height: 13px;
          }
        }
        &--instagram {
          svg {
            width: 17px;
            height: 17px;
          }
        }
      }
    }

    .banner__arrow {
      position: absolute;
      bottom: 0;
      right: 0;

      margin-top: 4rem;

      @include tablet {
        margin-top: auto;
      }

      .banner__arrow-link {
        padding: 0 0.75rem;
        transition: all 0.2s ease-in-out;

        &--left {
          &:hover {
            padding-right: 1rem;
          }
        }
      }
    }
  }

  .banner__image {
    grid-column: span 4;
    text-align: center;

    @include tablet {
      grid-column: span 7;
      text-align: left;
    }
  }

  .banner__scroll {
    display: none;
    position: relative;
    overflow: hidden;

    grid-column: span 1;

    @include desktop {
      display: block;
    }

    .banner__scroll-content {
      display: flex;
      align-items: center;
      position: absolute;
      bottom: -15px;
      left: 2rem;
      width: 15rem;
      transform: rotate(-90deg);
      transform-origin: 0;

      @include bgdesktop {
        left: 5rem;
      }
    }
  }

  .banner-scroll__title {
    position: absolute;
    right: -5rem;
    bottom: 210px;
    display: flex;
    align-items: center;
    transform: rotate(-90deg);
  }

  .banner-scroll__icon {
    transform: rotate(180deg);
  }

  .banner-scroll__text {
    font-size: 1.5rem;
    font-weight: 400;
    color: $black;

    padding-left: 1.1875rem;
  }

  .banner-social-icon {
    position: absolute;
    bottom: 115px;
    left: 2rem;

    width: 93px;

    .social-icon__link {
      &--facebook {
        svg {
          width: 9px;
          height: 17px;
        }
      }
      &--twitter {
        svg {
          width: 16px;
          height: 13px;
        }
      }
      &--instagram {
        svg {
          width: 17px;
          height: 17px;
        }
      }
    }
  }
}
