.page-title {
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 700;
  color: $black;
}

.page-title--small {
  font-size: 1.25rem;
  line-height: 1.5625rem;
  font-weight: 700;
  color: $black;

  @include desktop {
    font-size: 1.5rem;
    line-height: 1.875rem;
  }
}

.subtitle {
  font-size: 1rem;
  line-height: 1.5;
  letter-spacing: 0.03125rem;
  font-weight: 500;
  color: $black;
  text-transform: uppercase;
}
