.mission {
  .mission__container {
    padding: 2.5rem 0 3.75rem;
    background-color: $light-green;
    @include tablet {
      padding: 3.75rem 2.5rem 5rem;
    }
    @include desktop {
      padding: 5.625rem 8.375rem 7.5rem;
    }
  }

  .mission__title-block {
    display: flex;
  }

  .mission__title-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 32rem;

    border-bottom: 1px solid $basic-green;
    padding-bottom: 2rem;
    margin-bottom: 1.5rem;

    @include tablet {
      border: none;
      padding: 0;
    }
  }

  .mission__title {
    font-size: 2rem;
    line-height: 1.2;
    font-weight: 700;
    color: $black;

    @include tablet {
      font-size: 2.5rem;
      line-height: 2.875rem;
    }

    @include desktop {
      font-size: 3.0625rem;
      line-height: 3.5rem;
    }
  }

  .mission__info {
    margin-top: 1.5rem;
    font-size: 1.5rem;
    line-height: 1.875rem;
    font-weight: 700;
    color: $basic-green;

    @include tablet {
      margin-top: 0;
    }
  }

  .mission__description-intro {
    &:first-of-type {
      padding-top: 2.5rem;
      margin-bottom: 1.5rem;
    }
  }
}
