.card {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 2.1875rem;
  background-color: $light-gray;

  @include laptop {
    min-height: 34.375rem;
  }

  .card-subtitle {
    padding-bottom: 2.5rem;
    color: $basic-green;
  }

  .card-intro {
    padding: 2.5rem 0 0;

    @include laptop {
      padding: 2.5rem 0 0 2.5rem;
    }
  }

  .card-link {
    padding: 2rem 0 0 0;
    margin-top: auto;

    @include laptop {
      padding: 2rem 0 0 2.5rem;
    }
  }
}
