.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .main-nav__list {
    display: none;
    justify-content: space-between;
    padding-right: 6.25rem;

    @include desktop {
      display: flex;
    }
  }
  .main-nav__link {
    padding: 0 1.41rem;
    font-size: 1rem;
    font-weight: 400;
    color: $white;

    &:hover {
      text-decoration: underline;
    }
  }
}
