.invest-block {
  background-color: #ccd0d4;

  @include tablet {
    background-image: url(/img/covid-banner-tablet@1x.png);
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
      background-image: url(/img/covid-banner-tablet@2x.png);
    }
  }

  @include desktop {
    background-image: url(/img/covid-banner-desktop@1x.png);
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
      background-image: url(/img/covid-banner-desktop@2x.png);
    }
  }
}

.invest-block__content {
  @include tablet {
    padding: 3.125rem;

    max-width: 51.625rem;
    background-color: $white;
  }

  @include desktop {
    margin-left: 6rem;
  }
}

.invest-block__subtitle {
  margin-bottom: 0.625rem;
}

.invest-block__title {
  margin-bottom: 1.875rem;
  max-width: 34.375rem;
}

.invest-block__text {
  max-width: 43.75rem;
}

.invest-block__link {
  display: inline-block;
  padding-top: 4rem;

  font-size: 1.25rem;
  line-height: 1.5;
  font-weight: 700;
  color: $black;
  text-decoration: underline;

  &:hover {
    color: $basic-green;
  }

  @include desktop {
    padding-top: 6.25rem;
    font-size: 1.5rem;
  }
}
