.common-section {
  padding: 3.75rem 0;

  @include tablet {
    padding: 5rem 0;
  }

  @include desktop {
    padding: 6.25rem 0;
  }
}

.common-header {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2.5rem;

  .common-header__arrow-link {
    transition: all 0.2s ease-in;

    &--left {
      padding-right: 1.5rem;

      .common-header__arrow-icon {
        transform: rotate(180deg);
      }

      &:hover {
        padding-right: 2.5rem;
      }
    }
  }
}
