.header {
  position: static;
  top: 0;
  left:0;
  width: 100%;
  z-index: 1;

  @include tablet {
    position: absolute;
  }

  .header__wrapper {
    position: relative;
    margin: auto;
    padding-left: 20px;
    padding-right: 20px;

    @include tablet {
      padding-left: 30px;
      padding-right: 30px;
    }

    @include desktop {
      padding: 0 1rem;
      max-width: 1592px;
    }

    @include bgdesktop {
      max-width: 1680px;
    }
  }

  .header__container {
    justify-content: space-between;
    align-items: center;
  }

  .header__logo {
    img {
      width: 8.8125rem;
      height: 2.125rem;
    }

    &:hover {
      opacity: 0.8;
    }
  }

  .header__toggle {
    background-color: $basic-green;
    width: 60px;
    height: 60px;

    display: flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;

    &:hover {
      background-color: $hover-green;
    }

    @include desktop {
      width: 5.625rem;
      height: 5.625rem;
    }

    @include bgdesktop {
      width: 6.4375rem;
      height: 6.4375rem;
    }
  }
}
